
.entrance {
  height: 120px;
  padding: 35px 0;
}
.entrance .icon {
    font-size: 30px;
    line-height: 30px;
    width: 30px;
    height: 30px;
    margin-bottom: 6px;
}
.entrance span {
    font-size: 14px;
    line-height: 14px;
}
.avatar {
  margin-bottom: 10px;
  overflow: hidden;
}
.avatar img {
    float: left;
    width: 50px;
    border-radius: 50%;
    border: 2px solid #fff;
}
.avatar .info {
    margin-left: 10px;
    float: left;
}
.avatar .info .username {
      color: #4a90e2;
      font-size: 18px;
      line-height: 28px;
}
.avatar .info .card-num {
      color: #cc6e6e;
      font-size: 12px;
}
.navbar {
  border-color: #2275d7;
  background-color: #4a90e2;
  color: #fff;
}
.button {
  color: #fff;
}
.button:hover {
  color: #fff;
  text-decoration: none;
}
.bar .title {
  color: #fff;
}
.footer {
  height: 40px;
}
.sys-info {
  width: 100%;
  height: 20px;
  position: absolute;
  left: 0;
  bottom: -30px;
  color: #989da2;
}



